import React from 'react'
import './css/Ring.css'

const noop = () => {}

const Ring = ({
  size,
  styles = {},
  title = null,
  onMouseEnter = noop,
  onMouseLeave = noop,
  hovering = false,
  children
}) => {
  let style = {
    ...styles,
    width: `${size}px`,
    height: `${size}px`
  }

  return (
    <div
      className={`ring ${hovering ? 'hovering' : ''}`}
      style={style}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
    >
      {title && (
        <h4
          className={`ring-title ${hovering ? 'hovering' : ''}`}
        >
          {title}
        </h4>
      )}
      {children}
    </div>
  )
}

export default Ring
