import React, { useState, useEffect } from 'react'
import './css/Circle.css'

const xyOnCircle = ({ radius, angle, cx, cy }) => {
  angle = angle * (Math.PI / 180)
  const x = cx + radius * Math.cos(angle)
  const y = cy + radius * Math.sin(angle)
  return { x, y };
}

const noop = () => {}

const Circle = ({
  text = '',
  size,
  angle = null,
  ringRadius = null,
  link = null,
  picture = null,
  center = null,
  styles = {},
  offset = 0,
  spin = null,
  spinDirection = -1,
  setSpinning = noop,
  children
}) => {
  let [ cAngle, setAngle ] = useState(angle)
  let [ hovering, setHover ] = useState(false)

  useEffect(() => {
    let interval

    if (spin)
      interval = setInterval(() => {
        setAngle(cAngle + spinDirection)
      }, 20);

    return () => clearInterval(interval)
  })

  let style = {
    ...styles,
    width: `${size}px`,
    height: `${size}px`
  }

  if (!center && angle !== null && ringRadius !== null) {
    let { x, y } = xyOnCircle({
      radius: ringRadius,
      angle: cAngle,
      cx: ringRadius,
      cy: ringRadius
    })

    x -= (size / 2)
    y -= (size / 2)

    style.top = `${x + offset}px`
    style.left = `${y + offset}px`
  }

  if (hovering) {
    style.color = '#3df1f1'
    style.borderColor = '#3df1f1'
    style.boxShadow = '0px 0px 20px 0px hsla(208, 100%, 85%, 1.0)'
  }

  if (picture) {
    style.background = `url("${picture}") center center / contain no-repeat`
  }

  return (
    <div
      className="circle"
      style={style}
      onMouseEnter={() => {
        setHover(true)
        setSpinning(false)
      }}
      onMouseLeave={() => {
        setHover(false)
        setSpinning(true)
      }}
    >
      {children}
      <a
        style={{ color: style.color }}
        href={link}
        target="_blank"
        rel="noopener noreferrer"
      >
        {text}
      </a>
    </div>
  )
}

export default Circle
