import React from 'react'
import './css/Spinner.css'

const Spinner = ({ upperText, lowerText, children }) => {
  const renderEmpty = (num) => {
    const els = []

    for (let i = 0; i < num; ++i) {
      els.push(
        <div key={i} className="spinner__sector"></div>
      )
    }
    
    return els
  }

  return (
    <div className="spinner" onClick={() => console.log('click')}>
      {children}
      <div className="spinner__ring">
        {upperText.split('').map((c, i) => (
          <div key={i} className="spinner__sector">{c}</div>
        ))}
        {renderEmpty(30 - upperText.length)}
      </div>
      <div className="spinner__ring">
        {lowerText.split('').map((c, i) => (
          <div key={i} className="spinner__sector">{c}</div>
        ))}
        {renderEmpty(30 - lowerText.length)}
      </div>
    </div>
  )
}

export default Spinner
