import React, { useState } from 'react'
import Spinner from '../Spinner'
import Circle from '../Circle'
import Ring from '../Ring'
import './styles.css'

const InnerRing = ({
  children,
  circleSize,
  hovering,
  onMouseEnter,
  onMouseLeave
}) => {
  const ringSize = circleSize * 3.5 * 1.2
  const a = 360 / 4
  const angles = [ (a * 0) + 45, (a * 1) + 45, (a * 2) + 45, (a * 3) + 45 ]
  const [ spinning, setSpinning ] = useState(true)

  return (
    <Ring
      title="42 Projects"
      size={ringSize}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
      hovering={hovering}
    >
      {children}
      <Circle
        text="libft"
        link="https://github.com/coltonehrman/42sv_libft"
        angle={angles[0]}
        size={circleSize}
        ringRadius={ringSize / 2}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      />

      <Circle
        text="fillit"
        link="https://github.com/coltonehrman/42sv_fillit"
        angle={angles[1]}
        size={circleSize}
        ringRadius={ringSize / 2}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      />

      <Circle
        text="printf"
        link="https://github.com/coltonehrman/42sv_printf"
        angle={angles[2]}
        size={circleSize}
        ringRadius={ringSize / 2}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      />

      <Circle
        text="gnl"
        link="https://github.com/coltonehrman/42sv_getnextline"
        angle={angles[3]}
        size={circleSize}
        ringRadius={ringSize / 2}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      />
    </Ring>
  )
}

const RingTwo = ({
  circleSize,
  children,
  hovering,
  onMouseEnter,
  onMouseLeave
}) => {
  const ringSize = circleSize * 3.5 * 2.5
  const a = 360 / 5
  const angles = [ a * 0, a * 1, a * 2, a * 3, a * 4 ]
  const [ spinning, setSpinning ] = useState(true)
  
  return (
    <Ring
      title="C0D3 Projects"
      size={ringSize}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
      hovering={hovering}
    >

      <Circle
        text="Kanban"
        link="https://github.com/coltonehrman/js-kanban-board"
        angle={angles[0]}
        size={circleSize}
        ringRadius={ringSize / 2}
        spinDirection={1}
        styles={{ fontSize: '0.95rem' }}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      >
        <Circle
          text="JS"
          size={circleSize * 0.65}
          angle={(360 / 5) * 0}
          ringRadius={circleSize}
          offset={-(circleSize / 2)}
          styles={{ fontSize: '0.95rem' }}
          spin
        />
        <Circle
          text="HTML5"
          size={circleSize * 0.65}
          angle={(360 / 5) * 1}
          ringRadius={circleSize}
          offset={-(circleSize / 2)}
          styles={{ fontSize: '0.6rem' }}
          spin
        />
        <Circle
          text="CSS3"
          size={circleSize * 0.65}
          angle={(360 / 5) * 2}
          ringRadius={circleSize}
          offset={-(circleSize / 2)}
          styles={{ fontSize: '0.75rem' }}
          spin
        />
        <Circle
          text="OOP"
          size={circleSize * 0.65}
          angle={(360 / 5) * 3}
          ringRadius={circleSize}
          offset={-(circleSize / 2)}
          styles={{ fontSize: '0.95rem' }}
          spin
        />
        <Circle
          text="Local Storage"
          size={circleSize * 0.65}
          angle={(360 / 5) * 4}
          ringRadius={circleSize}
          offset={-(circleSize / 2)}
          styles={{ fontSize: '0.75rem' }}
          spin
        />
      </Circle>

      <Circle
        text="Mine Sweeper"
        link="https://github.com/coltonehrman/js-minesweeper"
        angle={angles[1]}
        size={circleSize}
        ringRadius={ringSize / 2}
        spinDirection={1}
        styles={{ fontSize: '0.65rem' }}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      >
      </Circle>

      <Circle
        text="Selection Box"
        link="https://github.com/coltonehrman/js-selection-box"
        angle={angles[2]}
        size={circleSize}
        ringRadius={ringSize / 2}
        spinDirection={1}
        styles={{ fontSize: '0.65rem' }}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      />

      <Circle
        text="Lights Out"
        link="https://github.com/coltonehrman/js-lights-out"
        angle={angles[3]}
        size={circleSize}
        ringRadius={ringSize / 2}
        spinDirection={1}
        styles={{ fontSize: '0.75rem' }}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      />

      <Circle
        text="Google Fair"
        link="https://github.com/coltonehrman/js-google-fair"
        angle={angles[4]}
        size={circleSize}
        ringRadius={ringSize / 2}
        spinDirection={1}
        styles={{ fontSize: '0.75rem' }}
        setSpinning={setSpinning}
        spin={hovering && spinning}
      />

      {children}
    </Ring>
  )
}

const Projects = ({
  scale,
  translateX,
  translateY
}) => {
  const hovering = [ useState(false), useState(false) ]
  const BASE_CIRCLE_SIZE = 50

  return (
    <div
      className="projects"
      style={{
        transform: `translate(${translateX}px, ${translateY}px) scale(${scale})`
      }}
    >
      <RingTwo
        circleSize={BASE_CIRCLE_SIZE * 1}
        hovering={hovering[0][0]}
        onMouseEnter={() => {
          hovering[0][1](true)
          hovering[1][1](false)
        }}
        onMouseLeave={() => hovering[0][1](false)}
      >
        <InnerRing
          circleSize={BASE_CIRCLE_SIZE}
          hovering={hovering[1][0]}
          onMouseEnter={() => {
            hovering[1][1](true)
            hovering[0][1](false)
          }}
          onMouseLeave={() => {
            hovering[1][1](false)
            hovering[0][1](true)
          }}
        >
          <Spinner
            upperText="PROJECTS"
            lowerText="PROJECTS"
          />
        </InnerRing>
      </RingTwo>
    </div>
  )
}

export default Projects
