import React from 'react'
import './css/ZoomControl.css'

const ZoomControl = ({
  onZoomIn,
  onZoomOut
}) => {
  return (
    <div className="mid">
      <label className="switch">
        <span
          className="switch-left"
          onClick={onZoomOut}
        >
          -
        </span>
        
        <span
          className="switch-right"
          onClick={onZoomIn}
        >
          +
        </span>
      </label>
    </div>
  )
}

export default ZoomControl
